body {
  padding: 0;
  margin: 0;
  background: #22252b;
  color: #fff;
  font: normal 18px/1.5 "IBM Plex Sans", Arial, Helvetica, sans-serif;
}

* {
  box-sizing: border-box;
}

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 40px;
}

.content {
  width: 100%;
  max-width: 540px;
}

.logo {
  font-size: 24px;
}

.slogan {
  color: #a4a4a7;
  margin-bottom: 20px;
}

.password {
  padding: 24px;
  margin-bottom: 16px;
  border-radius: 6px;
  background-color: #2c3036;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  font-size: 40px;
  font-family: "IBM Plex Mono", monospace;
  text-align: center;
}

.button {
  margin: 0;
  padding: 12px 20px;
  cursor: pointer;
  font: inherit;
  color: #fff;
  background-color: #0087ff;
  border: none;
  border-radius: 4px;
  outline: none;
}

.button:hover {
  background-color: #1490ff;
}

.button:active {
  transform: translateY(1px);
}
